.submit{
  background: #0D6EF1;
  color: white;
  height: 56px;
  margin-top: 30px;
  border: unset;
  border-radius: 5px;
  padding: 5px 25px;
  cursor: pointer;
}
.back{
  background: #8c8c8c;
  width: 95px;
  color: white;
  height: 56px;
  margin-top: 30px;
  border: unset;
  border-radius: 5px;
  padding: 5px 25px;
  cursor: pointer;
  a{
    color: white!important;
  }
}
.MuiFormControlLabel-root {
  margin-right: -11px!important;
  margin-left: 16px!important;
}

.custom-table{
  border: 1px solid white;
  margin-top: 20px;
  margin-bottom: 30px;
  thead{
    background: rgba(255, 255, 255, 0.50);
    tr{
      th{
        color: white;
      }
    }
  }

  tbody{
    tr{
      td{
        color: white;
      }
    }
  }

  .small-td{
    width: 5%;
  }
}

.each-value-box{
  background: rgba(216, 216, 216, 0.50);
  padding: 30px 15px;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 15px;
  .value{
    color: #2FCC9A;
    font-size: 18px;
  }
}

.new{
  background: #D8D8D8;
  border: none;
  border-radius: 5px;
  color: #2E2A54;
  padding: 5px 15px;
  font-size: 16px;
}

.info-box{
  background: #ffffff78;
  min-width: 17%;
  padding: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: relative;

  img{
    width: 45px;
  }
  .title{
    color: white;
    position: absolute;
    bottom: -33px;
    right: 0px;
    font-size: 14px;
  }
}

.info-box-small{
  background: #ffffff78;
  min-width: 11%;
  padding: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: relative;

  img{
    width: 45px;
  }
  .title{
    color: white;
    position: absolute;
    bottom: -33px;
    right: 0px;
    font-size: 14px;
  }
}

.delete-item{
  background: #f99b9b;
  min-width: 3%;
  padding: 5px;
  display: flex;
  align-items: center;
  justify-content: center;

  img{
    width: 100%;
  }
}

.makeStyles-paper-149 {
  padding: 0px!important;
}

.card-header{
  background: #2C313D;
}
.card-body-modal{
  background: #2C313D;
  width: 500px;
}
.card-footer{
  background: #2C313D;
}
.save{
  color: #2C313D;
  background: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
}

.cancel{
  background: rgba(154, 154, 154, 0.88);
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
}


.box{
  border: 2px solid whitesmoke;
  display: flex;
  padding: 15px;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
  img{
    width: 40px;
    margin-left: 10px;
  }
}
